/* FILE: custom-styles.scss */
/* FILE: custom/_custom.scss */
/* FILE: custom/__base.scss */
body {
	color: #6a6c6e;
	/*[*/
	background-color: #eaecee;
	/*]*/
	/*//
	background-color: [[background_color]];
	//*/
}

.site,
.page-section .posts .entry {
	background-color: #fff;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	color: #1a1c1e;
}

hr,
code,
pre {
	background-color: #eaecee;
	color: #6a6c6e;
	font-size: 20px;
	color: green;
}

button,
input,
select,
textarea,
tbody tr:hover td,
tbody tr:hover th {
	background-color: rgba(0, 0, 0, 0.05);
}

.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.2);
}

.button:active,
button:active,
input[type="button"]:active,
input[type="reset"]:active,
input[type="submit"]:active {
	box-shadow: inset 0 100px 0 rgba(0, 0, 0, 0.2);
}

.gallery-caption,
.widget .tagcloud a:before {
	background-color: #2a2c2e;
	color: #caccce;
}

.gallery-caption {
	background-color: rgba(0, 0, 0, 0.8);
}

::placeholder {
	color: inherit;
	opacity: .66;
}

:disabled::placeholder {
	color: inherit;
	opacity: .66;
}

::selection {
	background: #fafcbe;
	color: #6a6c2e;
}

/* FILE: custom/__accent.scss */
a,
.accent-color {
	/*[*/
	color: #0aac8e;
	/*]*/
	/*//
	color: [[color_accent]];
	//*/
}

mark,
ins,
.highlight,
pre::before,
.pagination a,
.pagination span,
.label-sticky,
.button,
button,
form button,
.fl-node-content button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.menu-group-nav a,
.post-navigation .nav-previous,
.post-navigation .nav-next,
.bypostauthor > .comment-body .comment-author::before,
.comment-navigation a,
.widget_calendar tbody a,
.widget .tagcloud a:hover,
body #infinite-handle span,
.rtb-message,
.site-content div.sharedaddy .sd-content ul li a.sd-button:not(.no-text) {
	/*[*/
	background-color: #0aac8e;
	color: #fff;
	/*]*/
	/*//
	background-color: [[color_accent]];
	color: [[color_accent_text]];
	//*/
}

mark,
ins,
.highlight {
	/*[*/
	box-shadow: 0.38em 0 0 #0aac8e, -0.38em 0 0 #0aac8e;
	/*]*/
	/*//
		box-shadow:  .38em 0 0 [[color_accent]],
		            -.38em 0 0 [[color_accent]];
		//*/
}

.post-navigation .nav-previous::before {
	/*[*/
	background: #0aac8e;
	background: linear-gradient(to left, transparent 19%, #0aac8e 81%);
	/*]*/
	/*//
		background: [[color_accent]];
		background: linear-gradient( to left, transparent 19%, [[color_accent]] 81% );
		//*/
}

[dir="rtl"] .post-navigation .nav-previous::before {
	/*[*/
	background: linear-gradient(to right, transparent 19%, #0aac8e 81%);
	/*]*/
	/*//
			background: linear-gradient( to right, transparent 19%, [[color_accent]] 81% );
			//*/
}

.post-navigation .nav-next::before {
	/*[*/
	background: #0aac8e;
	background: linear-gradient(to right, transparent 19%, #0aac8e 81%);
	/*]*/
	/*//
		background: [[color_accent]];
		background: linear-gradient( to right, transparent 19%, [[color_accent]] 81% );
		//*/
}

[dir="rtl"] .post-navigation .nav-next::before {
	/*[*/
	background: linear-gradient(to left, transparent 19%, #0aac8e 81%);
	/*]*/
	/*//
			background: linear-gradient( to left, transparent 19%, [[color_accent]] 81% );
			//*/
}

/* FILE: custom/__header.scss */
.site-header {
	/*[*/
	background-color: #1a1c1e;
	color: #fff;
	/*]*/
	/*//
	background-color: [[color_header]];
	color: [[color_header_text]];
	//*/
}

.nav-search-form .form-search {
	/*[*/
	background-color: #0aac8e;
	/*]*/
	/*//
		background-color: [[color_accent]];
		//*/
}

.nav-search-form .search-field {
	background-color: #fff;
	color: #6a6c6e;
}

/* FILE: custom/__menu.scss */
.main-navigation {
	border-color: #4a4c4e;
}

.main-navigation li ul {
	background-color: #3a3c3e;
	color: #fff;
}

.main-navigation .menu li li:hover > a,
.main-navigation .menu li li > a:hover,
.main-navigation .menu li li.active-menu-item > a {
	background-color: #4a4c4e;
}

@media only screen and (max-width: 54.9375em) {
	.main-navigation-inner {
		background-color: #3a3c3e;
	}
	.main-navigation ul a {
		color: #fff;
	}
	.main-navigation .menu a:hover,
	.main-navigation .menu li:hover > a,
	.main-navigation .menu li li:hover > a,
	.main-navigation .menu li.active-menu-item > a,
	.main-navigation .menu li li.active-menu-item > a {
		background: transparent;
		color: #fff;
	}
	.main-navigation .menu li.current-menu-item > a,
	.main-navigation .menu li li.current-menu-item > a {
		background-color: #4a4c4e;
	}
	.menu-toggle, .menu-toggle:hover, .menu-toggle:active, .menu-toggle:focus {
		background: transparent;
	}
	.menu-toggle::before {
		/*[*/
		background: #0aac8e;
		/*]*/
		/*//
			background: [[color_accent]];
			//*/
	}
}

/* FILE: custom/__banner.scss */
.site-banner {
	/*[*/
	background-color: #1a1c1e;
	color: #fff;
	/*]*/
	/*//
	background-color: [[color_front_widgets]];
	color: [[color_accent_text]];
	//*/
}

.custom-banner::before,
.site-banner-media::before {
	background: transparent;
	/*[*/
	background: linear-gradient(transparent, #1a1c1e);
	/*]*/
	/*//
	background: linear-gradient( transparent, [[color_front_widgets]] );
	//*/
}

/* FILE: custom/__sidebar.scss */
.front-page-widgets-wrapper {
	/*[*/
	background-color: #1a1c1e;
	color: #8a8c8e;
	/*]*/
	/*//
	background-color: [[color_front_widgets]];
	color: [[color_front_widgets_text]];
	//*/
}

/* FILE: custom/__footer.scss */
.site-footer {
	/*[*/
	background-color: #1a1c1e;
	color: #8a8c8e;
	/*]*/
	/*//
	background-color: [[color_footer]];
	color: [[color_footer_text]];
	//*/
}

/* FILE: custom/__plugins.scss */
.infinite-loader .spinner > div > div {
	/*[*/
	background-color: #0aac8e !important;
	/*]*/
	/*//
		background-color: [[color_accent]] !important;
		//*/
}

.site-content div.sharedaddy .sd-content ul li a.sd-button:not(.no-text) {
	/*[*/
	color: #fff !important;
	/*]*/
	/*//
		color: [[color_accent_text]] !important;
		//*/
}

/* FILE: custom/__borders.scss */
.site {
	border-color: #eaecee;
}

input:focus,
select:focus,
textarea:focus,
.label-sticky::before,
.label-sticky::after,
.posts .entry:hover,
.widget .tagcloud a:hover,
.site .picker__input.picker__input--active {
	/*[*/
	border-color: #0aac8e;
	/*]*/
	/*//
	border-color: [[color_accent]];
	//*/
}

